// import store from '../store/index'
import configInfo from '@/utils/config.js';
/**
 * X-APP-ID 1  2
 * X-CLIENT-ID  0APP 1APP 2WAP 3 4web
 */
export default {
  config: {
    // url
    baseUrl: configInfo.BASE_URL,
    header: {
      // 'Content-Type': 'application/x-www-form-urlencoded',
      'Content-Type': 'application/json',
      'X-APP-ID': '2',
      'X-CLIENT-ID': '3',
    },
    data: {},
    method: 'GET',
    dataType: 'json',
    /* json JSON.parse */
    responseType: 'text',
    hideLoading: false,
    hideMsg: false,
    success() {},
    fail() {},
    complete() {},
  },
  // 
  interceptor: {
    request: null,
    response: null,
  },
  request(options) {
    if (!options) {
      options = {};
    }
    const hideLoading = options.hideLoading || false; //  loading
    if (!hideLoading) {
      uni.showLoading({
        title: '...',
        mask: true,
      });
    }
    options.baseUrl = options.baseUrl || this.config.baseUrl;
    options.dataType = options.dataType || this.config.dataType;
    options.url = options.baseUrl + options.url;
    options.data = options.data || {};
    options.method = options.method || this.config.method;
    console.log(options);
    // todo 
    uni.setStorageSync('phone', '17610229358');

    if (options.url.indexOf('/mem/auth/') < 0) {
      const token = uni.getStorageSync('token'); //  token
      this.config.header.Authorization = 'bearer ' + token;
      console.log(token);
    } else {
      this.config.header.Authorization = '';
    }





    console.log(this.config.header);
    //  Promise 
    return new Promise((resolve, reject) => {
      uni.request({
        url: options.url,
        data: options.data,
        header: this.config.header,
        method: options.method,
        success: (response) => {
          let res = response.data;
          if (res.code === 100000 || res.code === 10000) {
            resolve(res);
          } else {
            //  200
            if (res.errCode) {
              this.showToast(res, parseInt(res.errCode));
            } else if (res.code === 500000) {
              this.showToast(res, res.code);
            } else {
              this.showToast(res);
            }
            reject('[' + res + '] ');
          }
        },
        fail: (error) => {
          console.log(error);
          if (error && error.response) {
            reject(error.response);
            this.showToast(error.response);
          }
        },
        complete: () => {
          if (!hideLoading) uni.hideLoading();
        },
      });
    });
  },
  // get
  get(options = {}) {
    options.method = 'GET';
    return this.request(options);
  },
  // post
  post(options = {}) {
    options.method = 'POST';
    return this.request(options);
  },
  // put
  put(options = {}) {
    options.method = 'PUT';
    return this.request(options);
  },
  // delete
  delete(options = {}) {
    options.method = 'DELETE';
    return this.request(options);
  },
  showToast(error, errorCode) {
    let errorMsg = '';
    console.log(error, errorCode);

    if (errorCode) {
      switch (errorCode) {
        case 400:
          errorMsg = '';
          break;
        case 403:
          errorMsg = '';
          break;
        case 404:
          errorMsg = '!';
          break;
        case 504:
          errorMsg = '';
          break;
        case 502:
          errorMsg = '';
          break;
        case 500000:
          errorMsg = ',';
          break;
        case 401:
          errorMsg = '';
          uni.redirectTo({
            url: '/pages/login/login',
          });
          return;
          break;
        default:
          errorMsg = ',';
          break;
      }
      if (process.env.NODE_ENV === 'development') {
        uni.showModal({
          title: '',
          content: JSON.stringify(error),
          showCancel: false,
          complete: function () {
            setTimeout(function () {
              uni.hideToast();
            }, 1000);
          },
        });
      } else if (process.env.NODE_ENV === 'production') {
        uni.showToast({
          title: errorMsg,
          icon: 'none',
          duration: 4000,
          complete: function () {
            setTimeout(function () {
              uni.hideToast();
            }, 1000);
          },
        });
      }
    } else {
      //todo 
      uni.showModal({
        title: '',
        content: JSON.stringify(error),
        showCancel: false,
        complete: function () {},
      });
    }
    return null;
  },
};
