/**
 * 
 * @param func 
 * @param wait 
 * @param options 
 * options  leadingfalse  trailing: false 
 */
function throttle (fn, wait = 500, options = {}) {
  let timer = null
  let previous = 0
  const throttled = function () {
    const now = +new Date()
    // remaining 
    if (!previous && options.leading === false) previous = now
    const remaining = wait - (now - previous)
    if (remaining < 0) {
      if (timer) {
        clearTimeout(timer)
        timer = null
      }
      previous = now
      fn.apply(this, arguments)
    } else if (!timer && options.trailing !== false) {
      timer = setTimeout(() => {
        previous = options.leading === false ? 0 : new Date().getTime()
        timer = null
        fn.apply(this, arguments)
      }, remaining)
    }
  }
  return throttled
}

/**
 * 
 * @param func 
 * @param wait 
 * @param immediate 
 */
function debounce (fn, wait = 500, immediate) {
  let timer = null
  return function () {
    if (timer) clearTimeout(timer)
    if (immediate) {
      // 
      const callNow = !timer
      timer = setTimeout(() => {
        timer = null
      }, wait)
      if (callNow) {
        fn.apply(this, arguments)
      }
    } else {
      timer = setTimeout(() => {
        fn.apply(this, arguments)
      }, wait)
    }
  }
}
/**
 * 
 * @param idCard 
 * @returns {userInfo}
 */
function getIdCardInfo (idCard) {
  const info = {
    sex: '',
    age: 0,
    birthday: ''
  }
  // 
  const isIdcard = /^\d{6}(((19|20)\d{2}(0[1-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])\d{3}([0-9]|x|X))|(\d{2}(0[1-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])\d{3}))$/
  if (!isIdcard.test(idCard)) {
    return new Error('')
  }
  // idCard.substring(6, 10) + "-" + idCard.substring(10, 12) + "-" + idCard.substring(12, 14);
  // 
  if (parseInt(idCard.substr(16, 1)) % 2 === 1) {
    info.sex = '0'
  } else {
    info.sex = '1'
  }
  // 
  const myDate = new Date()
  const mYear = myDate.getFullYear()

  // 
  if (idCard.length === 15) {
    const year =
      idCard.substring(6, 8) +
      '-' +
      idCard.substring(8, 10) +
      '-' +
      idCard.substring(10, 12)
    info.birthday = year
    info.age = mYear - parseInt(idCard.substring(6, 8))
  }

  if (idCard.length === 18) {
    const year =
      idCard.substring(6, 10) +
      '-' +
      idCard.substring(10, 12) +
      '-' +
      idCard.substring(12, 14)
    info.birthday = year
    info.age = mYear - parseInt(idCard.substring(6, 10))
  }
  return info
}

/** @dateTimeFliter 
 * @time[] 						Date  		
 * @part[,:0]				Number        :0: 1:  2:
 * @dateComplete[,:true] 	Boolean 	0:true:,false:
 * @timeComplete[,:true] 	Boolean 	0:true:,false:
 * @dateConnector[,:-] 		String 		  : - : /
 * @timeConnector[,::] 		String 		   : - : /
 * @hour12[,:false]          Boolean     12   : true:12   false:24
 * @return   '2019-11-25 15:05:54'  String    
 * **/
function dateTimeFliter (
  time,
  part = 0,
  dateComplete = true,
  timeComplete = true,
  dateConnector = '-',
  timeConnector = ':',
  hour12 = false
) {
  const year = time.getFullYear()
  let month = time.getMonth() + 1
  let day = time.getDate()
  let hour = time.getHours()
  let minute = time.getMinutes()
  let second = time.getSeconds()
  let dateStr = ''
  let timeStr = ''
  // 
  if (dateComplete) {
    // 0
    if (month < 10) {
      month = '0' + month
    }
    if (day < 10) {
      day = '0' + day
    }
  }
  dateStr = year + dateConnector + month + dateConnector + day
  // 
  // 
  if (hour12) {
    if (hour > 12) {
      hour = hour - 12
      if (timeComplete) {
        if (hour < 10) {
          hour = ' ' + '0' + hour
        } else {
          hour = ' ' + hour
        }
      }
    } else {
      if (timeComplete) {
        if (hour < 10) {
          hour = ' ' + '0' + hour
        } else {
          hour = ' ' + hour
        }
      }
    }
  }
  // 
  if (timeComplete) {
    // 0
    if (minute < 10) {
      minute = '0' + minute
    }
    if (second < 10) {
      second = '0' + second
    }
  }
  timeStr = hour + timeConnector + minute + timeConnector + second
  // 
  if (part === 0) {
    return dateStr + ' ' + timeStr
  } else if (part === 1) {
    return dateStr
  } else if (part === 2) {
    return timeStr
  }
  return ''
}

/** @getType 
 * @object[]    
 * @return   string/boolean/number...    
 * **/
function getType (object) {
  let result = null
  return ((result = typeof (object)) === 'object' ? object === null && 'null' ||
    Object.prototype.toString.call(object).slice(8, -1) : result)
    .toLowerCase()
}

/** @dateSurplus 
 * @startTime[]     Date      
 * @endTime[]     Date      
 * @format[,s]
 * @return   '800'  String    
 * **/
function dateSurplus (startTime, endTime = '0', format = '') {
  const start = new Date(startTime).getTime() / 1000
  let end = 0
  if (endTime) {
    end = new Date(endTime)
  } else {
    end = start + 86400
  }
  return end - start
}

/*
* @flatten 
* @param arr 
* @return Array 
* */
function flatten (arr) {
  let result = []
  for (let i = 0, len = arr.length; i < len; i++) {
    if (Array.isArray(arr[i])) {
      result = result.concat(flatten(arr[i]))
    } else {
      result.push(arr[i])
    }
  }
  return result
}
/*
* @unique 
* @param arr 
* @return Array 
* */
function unique (arr) {
  const appeard = new Set()
  return arr.filter(item => {
    // id
    const id = item + JSON.stringify(item)
    if (appeard.has(id)) {
      return false
    } else {
      appeard.add(id)
      return true
    }
  })
}

/**
 * 
 * @param {string} ,':'
 * @param {num} 
 * @param {string} :hms,h,m,s
 */
const timestampFormat = (t, f, format) => {
  var jsdate = t ? new Date(t * 1000) : new Date();
  var pad = function (n, c) {
    if ((n = n + '').length < c) {
      return new Array(++c - n.length).join('0') + n;
    } else {
      return n;
    }
  };
  var hours = pad(jsdate.getHours(), 2);
  var mins = pad(jsdate.getMinutes(), 2);
  var secs = pad(jsdate.getSeconds(), 2);
  switch (format) {
    case 'hms':
      return hours + f + mins + f + secs;
    case 'hm':
      return hours + f + mins;
    case 'ms':
      return parseInt(hours) * 60 + parseInt(mins) + f + secs;
  }
};
/**
 * 
 * @param dateDateTime
 * @param fmt   
 * @returns {}
 */
const formatDate = (date, fmt) => {
  var date = new Date(parseInt(date) * 1000);
  if (fmt == 'yyMMddhhmmss') {
    var nYear = date.getFullYear(),
      nMonth = date.getMonth() + 1,
      nDay = date.getDate(),
      nHours = date.getHours(), //
      nMinutes = date.getMinutes(), //
      nSeconds = date.getSeconds();

    nMonth = (nMonth < 10 ? '0' + nMonth : nMonth).toString();
    nDay = (nDay < 10 ? '0' + nDay : nDay).toString();
    nHours = (nHours < 10 ? '0' + nHours : nHours).toString();
    nMinutes = (nMinutes < 10 ? '0' + nMinutes : nMinutes).toString();

    //return nYear + nMonth + nDay + nHours + nMinutes;
    return nYear + nMonth + nDay + '_' + nHours + nMinutes;
  } else if (fmt == 'yyyyMMdd_hhmm') {
    var nYear = date.getFullYear(),
      nMonth = date.getMonth() + 1,
      nDay = date.getDate(),
      nHours = date.getHours(), //
      nMinutes = date.getMinutes(), //
      nSeconds = date.getSeconds();

    nMonth = (nMonth < 10 ? '0' + nMonth : nMonth).toString();
    nDay = (nDay < 10 ? '0' + nDay : nDay).toString();
    nHours = (nHours < 10 ? '0' + nHours : nHours).toString();
    nMinutes = (nMinutes < 10 ? '0' + nMinutes : nMinutes).toString();

    return nYear + nMonth + nDay + '_' + nHours + nMinutes;
  } else {
    var o = {
      'M+': date.getMonth() + 1, //
      'd+': date.getDate(), //
      'h+': date.getHours(), //
      'm+': date.getMinutes(), //
      's+': date.getSeconds(), //
      'q+': Math.floor((date.getMonth() + 3) / 3), //
      S: date.getMilliseconds(), //
    };

    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + '').substr(4 - RegExp.$1.length)
      );
    }

    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        );
      }
    }
    return fmt;
  }
};
/**
 * 
 * @returns {}
 */
const getWeek = (days) => {
  let week
  switch (days) {
    case 1:
      week = ''
      break
    case 2:
      week = ''
      break
    case 3:
      week = ''
      break
    case 4:
      week = ''
      break
    case 5:
      week = ''
      break
    case 6:
      week = ''
      break
    case 0:
      week = ''
      break
  }
  return week
};
/**
 * 
 * @param dateDateTime
 * @param fmt   
 * @returns {}
 */
const formatWeekInfo = () => {
  const today = new Date()
  const info = []
  for (let index = 0; index < 7; index++) {
    const infoChild = {}
    const nextDay = new Date(today)
    nextDay.setDate(today.getDate() + index)
    //
    const week = getWeek(nextDay.getDay())
    const isToday = getWeek(today.getDay())
    infoChild.week = isToday == week ? '' : week
    //
    infoChild.time = nextDay.getDate()
    //
    infoChild.infoTime =
      nextDay.getFullYear() +
      '/' +
      (nextDay.getMonth() + 1) +
      '/' +
      nextDay.getDate()
    infoChild.id = index
    info.push(infoChild)
  }
  return info
};

module.exports = {
  throttle,
  debounce,
  getType,
  getIdCardInfo,
  dateTimeFliter,
  dateSurplus,
  flatten, // 
  unique, // 
  formatDate,
  timestampFormat,
  formatWeekInfo
}
