import http from '@api/request.js'
/**
 *  
 *  @return {Array}
 */
export const login = (data, code) => {
  return http.post({
    url: `/mem/auth/v1/weChat/applet/auth/${code}`,
    data,
    hideLoading: false
  });
};

/**
 *  
 *  @return {Array}
 */
export const phoneLogin = (data, code) => {
  return http.post({
    url: `/mem/auth/v1/weChat/applet/phone/${code}`,
    data,
  });
};
